import React from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import * as dealDetailsActions from "../../actions/dealDetailsActions";
import Row from "muicss/lib/react/row";
import Col from "muicss/lib/react/col";
import ClientModal from "../common/ClientModal";
import { isEmpty } from "../../lib";

class DealContacts extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      modalOpen: false,
      contact: {},
    };
    this.openModal = this.openModal.bind(this);
    this.afterOpenModal = this.afterOpenModal.bind(this);
    this.closeModal = this.closeModal.bind(this);
  }

  getContactFullInfo(id) {
    this.props.actions.getFullContactInfo(id);
    this.setState({
      contact: this.props.dealDetails.contact,
    });
  }

  openModal() {
    this.getContactFullInfo(this.props.id);
    this.setState({
      modalOpen: true,
    });
  }

  afterOpenModal() {}

  closeModal() {
    this.setState({
      modalOpen: false,
    });
  }

  render() {
    return (
      <div className="deal-contacts">
        <div className="panel mui-panel" onClick={this.openModal}>
          <Row>
            <Col md="1" sm="1" xs="2">
              <div className="first-letter">
                {this.props.contactName.charAt(0)}
              </div>
            </Col>
            <Col md="11" xs="10">
              <Col md="2">
                <div className="contact-name">{this.props.contactName}</div>
              </Col>
              <Col md="3">
                <div className="contact-position">
                  {" "}
                  {this.props.contactPosition}
                </div>
              </Col>
            </Col>
          </Row>
          <ClientModal
            isOpen={this.state.modalOpen}
            onRequestClose={this.closeModal}
            onAfterOpen={this.afterOpenModal}
            name={this.props.contactName}
            loading={this.props.dealDetails.loadingContact}
            position={this.props.contactPosition}
            contact={this.props.dealDetails.contact}
            noContact={isEmpty(this.props.dealDetails.contact)}
          />
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    dealDetails: state.dealDetails,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(dealDetailsActions, dispatch),
  };
}
export default connect(mapStateToProps, mapDispatchToProps)(DealContacts);
