import React from "react";
import DealContacts from "./DealContacts";
import Modal from "react-modal";

class DealContactsPage extends React.Component {
  render() {
    const dealContacts =
      !this.props.loading &&
      this.props.contacts.map((item) => (
        <DealContacts
          contact={this.props.contact}
          key={item.id}
          id={item.id}
          contactName={item.contact_name}
          contactPosition={item.contact_position}
        />
      ));

    return (
      <div className="deal-contacts-page">
        <Modal
          className="loading-modal"
          isOpen={this.props.loading}
          ariaHideApp={false}
          contentLabel="Modal"
          shouldCloseOnOverlayClick={false}
        >
          <div className={this.props.loading ? "centered-spinner" : "hidden"}>
            <img
              src="/images/icons/spin.svg"
              className="loading-spinner"
              alt="loading spinner"
            />
          </div>
        </Modal>
        <div className={this.props.noContacts ? "" : "hidden"}>
          <div>
            <div className="grey-circle">
              <i className="material-icons">warning</i>
            </div>
            <h2 className="no-results">This deal has no deal contacts</h2>
          </div>
        </div>
        {dealContacts}
      </div>
    );
  }
}
export default DealContactsPage;
