import React from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import * as dealDetailsActions from "../../actions/dealDetailsActions";
import DealContactsPage from "./DealContactsPage";
import getParameterByName from "../common/getParameterByName";

class DealContactsContainer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      contact: {},
    };
  }

  componentDidMount() {
    this.props.actions.loadDealContacts(
      this.props.match.params.dealId
        ? this.props.match.params.dealId
        : getParameterByName("deal_id")
        ? getParameterByName("deal_id")
        : ""
    );
  }

  render() {
    return (
      <DealContactsPage
        contacts={this.props.dealDetails.contacts}
        loading={this.props.dealDetails.loading}
        noContacts={this.props.dealDetails.noContacts}
      />
    );
  }
}

function mapStateToProps(state) {
  return {
    dealDetails: state.dealDetails,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(dealDetailsActions, dispatch),
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(DealContactsContainer);
